body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
body {
  background-color: #2e856e;
  color: beige;
}
* {
  scroll-behavior: smooth;
}
.header-img {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  width: 100%;
  height: 55vh;
}
.heading-section {
  color: beige;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.9), 0 6px 20px 0 rgba(0, 0, 0, 0.9);
}
.heading-name {
  font-size: 35px;
}
nav {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
li .nav-link {
  font-size: 2rem;
  color: #5c4033;
  border-right: #5c4033 dotted;
}
li .nav-link:hover {
  color: beige;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.9), 0 6px 20px 0 rgba(0, 0, 0, 0.9);
}
Link .nav-link {
  font-size: 2rem;
  color: #5c4033;
  border-right: #5c4033 dotted;
}

Link .nav-link:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.9), 0 6px 20px 0 rgba(0, 0, 0, 0.9);
}
.navbar-color {
  background: url(data:image/jpeg;base64,);
}

.heading-section h1 {
  color: beige;
  font-family: "Amiri";
}
.heading-name {
  font-size: 30px;
}
.heading-lead {
  padding-top: 0%;
  font-size: 20px;
}
.embed-section {
  margin-top: 25px;
}
.calendar-section {
  background: url(data:image/jpeg;base64,);
  margin-top: 25px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.9), 0 6px 20px 0 rgba(0, 0, 0, 0.9);
}
.mosque {
  margin-top: 25px;
  background-color: #2e856e;
}
.three-col-section {
  background: url(data:image/jpeg;base64,);
  margin-top: 25px;
}

.mixlr-section {
  height: 215px;
  background: url(data:image/jpeg;base64,);
  margin-top: 25px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.9), 0 6px 20px 0 rgba(0, 0, 0, 0.9);
}
.telegram-section {
  background: url(data:image/jpeg;base64,);
  margin-top: 25px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.9), 0 6px 20px 0 rgba(0, 0, 0, 0.9);
}
.latest-section {
  height: 215px;
  background: url(data:image/jpeg;base64,);
  margin-top: 25px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.9), 0 6px 20px 0 rgba(0, 0, 0, 0.9);
  padding-top: 30px;
  padding-left: 30px;
}
.mixlr-img-section {
  height: 215px;
  background: url(data:image/jpeg;base64,);
  margin-top: 25px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.9), 0 6px 20px 0 rgba(0, 0, 0, 0.9);
}
.calendar {
  height: 215px;
}
.mixlr {
  background-color: beige;
  height: 215px;
}
img .logo {
  height: 50px;
  width: 50px;
}

.display-head {
  margin-top: 25px;
  font-family: "Gloria Hallelujah", sans-serif;
  color: beige;
  font-size: 30px;
}
.display-title {
  font-family: "Fredoka One", sans-serif;
  color: beige;
  text-shadow: 2px 2px 4px #000000;
}

.displayimg {
  height: 215px;
  width: 1000px;
  border: 2px #5c4033;
}
.audio-display-head {
  margin-top: 25px;
  font-family: "Amiri", sans-serif;
  font-size: 40px;
}
.rounded-links {
  border: 9px solid #5c4033;
  height: 9rem;
  width: 9rem;
  background-color: beige;
  background-image: url(data:image/jpeg;base64,);
}

a .rounded-headname {
  font-family: "Righteous", sans-serif;
  color: #5c4033;
  font-size: 1.7rem;
}
a .rounded-headname:hover {
  text-decoration: none;
}
a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:active {
  text-decoration: none;
}
.btn {
  width: 170px;
}
.card-header {
  font-family: "Tajawal", sans-serif;
  font-size: 30px;
}
.card-header2 {
  font-size: 40px;
}
.card {
  color: #5c4033;
  background-image: url(data:image/jpeg;base64,);
}
#menu {
  position: fixed;
  right: 2rem;
  top: 80%;
  width: 8em;
}

.caption {
  font-size: 30px;
}
.iframe {
  width: 1200px;
  object-fit: cover;
}
.aboutheader {
  font-family: "Langar", sans-serif;
  color: #5c4033;
  text-shadow: beige;
}
.about {
  font-size: 20px;
  font-family: "Fredoka", sans-serif;
}
.orderlist {
  font-family: "Poppins", sans-serif;
  font-size: 20px;
}

.responsiveArchives {
  position: absolute;
  top: 100px;
  left: 0;
  right: 0;
  bottom: 0;
}
.responsiveArchives iframe {
  display: block;
  width: 100%;
  height: 100%;
  border: none;
}
.responsiveCal {
  position: relative;
  padding-bottom: 40%;
  height: 100%;
  overflow: hidden;
}
.responsiveCal iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.mixlricon {
  height: 40px;
  width: 40px;
}
.mixlrtext {
  font-size: 25px;
  font-family: "Poppins", sans-serif;
  color: #5c4033;
  padding-top: 10px;
}
.icon-bar {
  display: none;
}
.timeline {
  margin-top: 40px;
  margin-bottom: 40px;
  position: relative;
  box-sizing: border-box;
}
.timeline::before {
  content: "";
  position: absolute;
  left: 50%;
  width: 2px;
  height: 100%;
  background-color: beige;
}
.timeline ul {
  margin: 0;
  padding: 0;
}
.timeline ul li {
  list-style: none;
  position: relative;
  width: 50%;
  padding: 20px 40px;
}
.timeline ul li:nth-child(odd) {
  float: left;
  text-align: right;
  clear: both;
}
.timeline ul li:nth-child(even) {
  float: right;
  text-align: left;
  clear: both;
}
.content {
  padding-bottom: 20px;
  border: 1px solid beige;
  padding: 10px 10px;
  border-radius: 5px;
  background: #fff;
  text-align: justify;
}
.timeline ul li:nth-child(odd)::before {
  content: "";
  position: absolute;
  top: 24px;
  right: -11px;
  width: 20px;
  height: 20px;
  background: #c23941;
  border-radius: 50%;
  box-shadow: 0 0 0 3px rgba(233, 33, 99, 0.2);
}
.timeline ul li:nth-child(even)::before {
  content: "";
  position: absolute;
  top: 24px;
  left: -9px;
  width: 20px;
  height: 20px;
  background: #c23941;
  border-radius: 50%;
  box-shadow: 0 0 0 3px rgba(233, 33, 99, 0.2);
}
.timeline ul li h3 {
  margin: 0;
  padding: 0;
  font-weight: 600;
  color: beige;
}
.timeline ul li p {
  margin: 10px 0 0;
  padding: 0%;
  line-height: 1.7;
  color: #2e856e;
}
.timeline ul li h4 {
  margin: 0;
  padding: 0;
  font-size: "16px";
}
.timeline ul li:nth-child(odd) .time {
  position: absolute;
  top: 12px;
  right: -165px;
  margin: 0;
  padding: 8px 16px;
  background: #303243;
  color: beige;
  border-radius: 18px;
}
.timeline ul li:nth-child(even) .time {
  position: absolute;
  top: 12px;
  left: -165px;
  margin: 0;
  padding: 8px 16px;
  background: #303243;
  color: beige;
  border-radius: 18px;
}
.phoneicon {
  display: none;
}
.subscribe-links {
  border: 9px solid #5c4033;
  height: 9rem;
  width: 9rem;
  background-color: beige;
  background-image: url(data:image/jpeg;base64,);
}

@media (min-width: 1200px) {
}
@media (max-width: 991.98px) {
  .latest-section {
    height: 215px;
  }
  .phoneicon {
    display: none;
  }
}

@media (max-width: 767.98px) {
  .heading-name {
    font-size: 25px;
  }
  .heading-lead {
    padding-top: 0%;
    font-size: 15px;
  }
  li .nav-link {
    font-size: 1.6rem;
    border-right: none;
  }
  .caption {
    font-size: 40px;
  }
  .responsiveCal {
    position: relative;
    padding-bottom: 100%;
  }
  .subscribe-links {
    border: 5px solid #5c4033;
    height: 6rem;
    width: 6rem;
  }

  .responsiveCal iframe {
    position: absolute;
  }
  .mixlricon {
    height: 25px;
    width: 25px;
  }
  .mixlrtext {
    font-size: 20px;
  }
}

@media (max-width: 575.98px) {
  .card-header {
    font-size: 35px;
  }
  .heading-name {
    font-size: 20px;
  }
  .heading-lead {
    padding-top: 0%;
    font-size: 15px;
  }
  li .nav-link {
    font-size: 1.4rem;
    border-right: none;
  }
  #menu {
    display: none;
  }

  .btn {
    width: 120px;
    font-size: 14px;
    margin-top: 5px;
  }
  .card {
    font-size: 15px;
    margin: 3px;
  }
  .figure-caption {
    color: beige;
    font-size: 20px;
    font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  }
  .caption {
    font-size: 10px;
  }
  .responsiveCal {
    position: relative;
    padding-bottom: 100%;
  }
  .aboutheader {
    font-size: 30px;
  }
  .about {
    font-size: 16px;
    margin: 5px;
  }

  .responsiveCal iframe {
    position: absolute;
  }
  .boardcard {
    font-size: 15px;
  }
  .mixlricon {
    display: block;
    height: 30px;
    width: 30px;
  }
  .mixlrtext {
    display: none;
  }
}

@media (prefers-reduced-motion: no-preference) {
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

